/* App.css */

body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f4f4f9;
  color: #333;
  line-height: 1.6;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  text-align: center;
  padding: 20px 0;
  background-color: #3f51b5;
  color: white;
}

button {
  cursor: pointer;
  background-color: #3f51b5;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  font-size: 1rem;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #303f9f;
}

input {
  padding: 10px;
  font-size: 1rem;
  width: calc(100% - 24px);
  max-width: 300px;
  margin-right: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* Layout */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.grid {
  display: grid;
  gap: 20px;
}

.grid-2 {
  grid-template-columns: repeat(2, 1fr);
}

.grid-3 {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

/* Components */
.movie-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.movie-card img {
  max-width: 100%;
  border-radius: 4px;
}

.movie-card h3 {
  margin: 10px 0 5px;
  font-size: 1.1rem;
  text-align: center;
}

.movie-card:hover {
  transform: scale(1.05);
}

.movie-details {
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

.movie-details img {
  float: left;
  margin-right: 20px;
  border-radius: 4px;
}

.movie-details h2 {
  margin-bottom: 10px;
}

.movie-details p {
  margin: 5px 0;
  line-height: 1.4;
}

.favorites {
  margin-top: 30px;
}

.favorites h2 {
  margin-bottom: 15px;
}

.favorites .favorite-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  padding: 10px 15px;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.favorites .favorite-item button {
  margin-left: 15px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .movie-details img {
    float: none;
    margin: 0 auto 15px;
    display: block;
  }

  input {
    width: 100%;
    margin-bottom: 10px;
  }

  button {
    width: 100%;
  }

  .grid-2, .grid-3 {
    grid-template-columns: 1fr;
  }
}
